/* eslint-disable */ 
import axios from 'axios';

// import  Vue  from  'vue'
import { router } from './router';

const API_URL = "http://127.0.0.1:8000/";

const ACCESS_TOKEN = localStorage.getItem("accessToken");
const PAGE = 1


export default class APIService{

    constructor(){
       this. applyTokenInHeader()
    }

    applyTokenInHeader(){
        if(ACCESS_TOKEN){
            // eslint-disable-next-line
            // console.dir(ACCESS_TOKEN);
            // Vue.prototype.$http.defaults.headers.common['Authorization'] = accessToken;
            
            axios.defaults.headers.common['Authorization'] ='JWT '+ ACCESS_TOKEN;
            // eslint-disable-next-line
            console.dir(axios);



        }
    }

 

    loginUser(username,pass,ctx){
        const url  = `${API_URL}auth/obtain_token/`;
        // const url2  = `${API_URL}api/init/`;

        axios.post(url,{username:username,password:pass}).then(function(response){
            localStorage.setItem("accessToken",response.data["token"]);
            axios.defaults.headers.common['Authorization'] ='JWT '+ response.data["token"];


            router.push("/");
            // eslint-disable-next-line
            console.log(response.data);
        });
    }


    sendMessage(message){
        
        const url  = `${API_URL}api/chat/`




        



        const post_result =  axios.post(url,message,{
            'Authorization':'JWT '+ACCESS_TOKEN,
            'Content-Type': 'application/x-www-form-urlencoded',
        });
        var chatContainer = document.getElementById("chat-cont");

        console.dir(post_result)

        chatContainer.scrollTop = chatContainer.scrollHeight;






    }


    getChatMessages(){
        const url  = `${API_URL}api/chat/`
        console.log(ACCESS_TOKEN)

        try{
            return axios.get(url,{
                headers:{
                    Authorization:'JWT '+ACCESS_TOKEN
                }
            })
        }catch(error){
            this.console.log(error)
        }
    }







}
