<template>

<div class="chat-root">
    <div class="chat-container" id="chat-cont">
        

            <div v-for="(msg,index) in chat" :key="index"  >
                <p class="msg-user"> {{ msg['user']==1?'Alex':'Sheryl' }}</p>
                <div class="chat-msg">
                     {{ msg['message'] }}
                    <p class="msg-time">


                        {{new Date( msg['updated']) }}
                    </p>                      
                </div>
                       
            
                       


            </div>

    </div>

    <div class="chat-writer">

                        <input type="text" placeholder="Write message here... " id="msg_input" class="inputstyle" v-model = "chatMsg"/>

                        <input type="button" value="SEND" class="login_button" id="send_msg"  @click.prevent="sendMessage"/>
        
    </div>

</div>
    
</template>

<style>



#msg_input{
    min-width: auto !important;
    display: inline !important;
    padding: 0 !important;
    margin: 0 !important;
    min-height: 40px !important;
    width: 68% !important;
    }

#send_msg{
    min-width: auto !important;
    display: inline !important;
    padding: 0 !important;
    margin: 0 !important;
    width: 6em !important;

}

.msg-user{
    font-family: Advent Pro;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    text-align: left;
    color: #000;
    margin-left: 16px; 
    margin-top: 4px;
    margin-bottom: 4px;

}

.msg-time{
    font-family: Advent Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 8px;
    line-height: 10px;

    color: #FFFFFF;    

}

.chat-msg{
    overflow-wrap: break-word;
    margin-left:8px;
    margin-right:8px;
    margin-top:0px;

    background-color: #2E2E2E;
    font-family: Advent Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;    
    color: #FFFFFF;
    border-radius: 8px;

    padding: 8px;
    margin-bottom: 1.4em;
}

.chat-root{
    display: flex;
    flex-direction: column;
}

.chat-container{
    width: auto;
    flex-grow: .8;
    margin: 8px;
    background-color: #F4F4F4;
    height: 100%;
    overflow-y: scroll;
    padding-bottom: 8em;
}

.chat-writer{
    margin-bottom: 8px;
    background-color: #383838;
    flex-grow: .2;
    margin-left: 8px;
    margin-right: 8px;
    display: flex;
    justify-content: space-between;
}

</style>

<script>
import APIService from './../APIService.js'
const apiServiceObj = new APIService();


export default {
    data:function(){
        return{
            chat :[],
            chatMsg:'',

        }
    },
    

  created(){
    // console.log("Chatroom.Vue   ......Created...")
    this.$emit('messsageFromChild',"true");
    

    this.loadMessages()

    // console.dir(this.chat)
    
  },

  methods:{

      sendMessage(){
          apiServiceObj.sendMessage({'chat_reply':this.chatMsg})
          this.chatMsg = ""
          this.loadMessages()

      },


      async getChatMessages(){
          let x = await apiServiceObj.getChatMessages();
          let data = x['data']
        //   console.dir(data)
          this.chat = data
      },


      loadMessages(){
          this.getChatMessages()

          setInterval(function(){
              this.getChatMessages()
            //   console.log("Counting.....")
          }.bind(this),3000)
      }
  }


}
</script>