import Vue from 'vue'
import Vuex from 'vuex'
import APIService from '../APIService'

Vue.use(Vuex)

const apiService = new APIService()

export const store = new Vuex.Store({
    namespaced: true,


    state: {
        JWT:localStorage.getItem('token'),
        feedList:{},
        initDataResponse:[],
        userData:[],
        userActivity: [],


    },

    mutations: {

        setJWT (state,payload){
            state.JWT = payload
        },

    },

    actions: {

        loginUser(context,userNamePass){
            // console.log(userNamePass)
            const token =apiService.loginUser(userNamePass.username,userNamePass.password,userNamePass.ctx)
            context.commit('setJWT',token)

           

        },


    }



});
